<template>
	<view class="CartParent">
		<!-- 头部地址区域 -->
		<view class="createTop">
			<view class="createTop_null">
				<p class="iconfont icon-location"></p>
			</view>
			<!-- 显示地址区域 -->
			<view v-if="adressList" class="Adress">
				<view class="Adress_top">
					<text>{{adressList.contactor_name}}</text>
					<text>{{adressList.phone}}</text>
				</view>
				<view class="Adress_bottom">
					<text>{{adressList.address}}</text>
					<text>{{adressList.street}}</text>
				</view>
			</view>
			<navigator class="createTop_null" url="../Receiving_address/Receiving_address">
				<image src="../../static/index/icon-right.png"></image>
			</navigator>
			<!-- 那条线 -->
			<view class="LineBottom">
				<image src="../../static/image/line.png"></image>
			</view>
		</view>
		<!-- 商品信息区域 -->
		<view class="ProductInformation" v-if="createList.length!==0">
			<!-- 左边图片区域 -->
			<image class="InformationLeft" mode="aspectFill" :src="InformationImg"></image>
			<!-- 右边文字区域 -->
			<view class="InformationRight">
				<p><text>{{createList.product.title}}</text></p>
				<p><text>{{createList.sku.value}}</text></p>
				<p><text>￥{{createList.sku.price}}<span>x{{createList.quantity}}</span></text></p>
			</view>
		</view>
		<!-- 商品金额区域 -->
		<view class="ProductPrice">
			<view class="ProductPAll">
				<text>商品全额</text>
				<p>￥ <span>0</span></p>
			</view>
			<view class="FullDiscount ProductPAll">
				<text>优惠全额</text>
				<p>- ￥ <span>0</span></p>
			</view>
			<view class="ProductPAll">
				<text>运费</text>
				<p>￥ <span>免运费</span></p>
			</view>
			<view class="Remarks">
				<text>备注</text>
				<input placeholder="请填写备注信息" />
			</view>
		</view>
		<!-- 下方的“实付款”区域-->
		<view class="ActualPayment">
			<p>实付款</p>
			<text>￥ -</text>
			<button class="Submit">提交订单</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				// 订单信息
				createList:[],
				// 图片路径
				InformationImg:'',
				// 地址数据
				adressList:{}
			}
		},
		onLoad(options) {
			// console.log(options)
			// 获取产品信息接口
			this.getProducInformation(options)
			this.getAdress()
		},
		methods: {
			async getProducInformation(options){
				// 发送请求
				const res=await this.$myRequest({
					url:'/cart',
					header:uni.getStorageSync("token"),
					data:{sku_id:options.sku_id,quantity:options.quantity}
				})
				// console.log(res)
				this.createList=res.data.data[0]
				// console.log(this.createList)
				// 获取图片路径
				this.InformationImg=this.createList.product.image[0]
			},
			// 获取收货地址的接口
			async getAdress(){
				const res=await this.$myRequest({
					url:'/user/getAddress',
					header:uni.getStorageSync("token")
				})
				// console.log(res.data.data)
				// 现在是要把数据做判断，先看有没有默认的地址，
				const Add=res.data.data.filter((item)=>{
					return item.is_default==1
				})
				// 因为filter是数组的方法，对象外面会包裹一层数组，后期不好拿数据
				this.adressList=Add[0]
				// 如果没有就先把第一个地址默认
				if(this.adressList==''){
					this.adressList=res.data.data[0]
				}
				// console.log(this.adressList)
			}
		}
	}
</script>

<style lang="less">
.CartParent{
	.createTop{
		width: 100%;
		background-color: #FFFFFF;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 30rpx;
		box-sizing: border-box;
		position: relative;
		margin-bottom: 20rpx;
		.createTop_null{
			width: 40px;
			padding: 15rpx 0;
			box-sizing: border-box;
			image{
				width: 65rpx;
				height: 63rpx;
			}
		}
		.Adress{
			width: 80%;
			.Adress_top{
				width: 100%;
				// 后面会变化的最好不要用高度，用padding更好
				padding: 5rpx 0;
				text:nth-child(1){
					font-size: 35rpx;
					margin-right: 20rpx;
				}
				text:nth-child(2){
					font-size: 30rpx;
				}
			}
			.Adress_bottom{
				width: 100%;
				padding: 25rpx 0;
				text{
					font-size: 30rpx;
					color: #909399;
				}
				text:nth-child(1){
					margin-right: 15rpx;
				}
			}
		}
		.LineBottom{
			width: 100%;
			position: absolute;
			bottom: -10rpx;
			left: 0rpx;
			image{
				width: 100%;
				height: 6rpx;
			}
		}
	}
	.ProductInformation{
		width: 100%;
		height: 190rpx;
		background-color: #FFFFFF;
		padding: 0 30rpx 20rpx 30rpx;
		box-sizing: border-box;
		display: flex;
		justify-content: space-between;
		align-items: center;
		.InformationLeft{
			width: 25%;
			height: 100%;
		}
		.InformationRight{
			width: 72%;
			height: 100%;
			p{
				width: 100%;
				height: 25%;
				margin-top: 10rpx;
			}
			p:nth-child(1){
				// 多余部分变成小圆点,设置在父级
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				font-size: 30rpx;
			}
			p:nth-child(2){
				font-size: 28rpx;
				color: #888888;
			}
			p:nth-child(3){
				font-size: 35rpx;
				text{
					span{
						font-size: 30rpx;
						color: #666666;
						margin-left: 22rpx;
						letter-spacing: 10rpx;
					}
				}
			}
		}
	}
	.ProductPrice{
		width: 100%;
		height: 370rpx;
		background-color: #FFFFFF;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		margin-top: 15rpx;
		.ProductPAll{
			width: 90%;
			height: 50px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom: 1px solid rgba(0,0,0,0.07);
			text{
				color:#888888;
				line-height: 20rpx;
				font-size: 30rpx;
			}
		}
		.FullDiscount{
			p{
				color:#fa436a;
			}
		}
		.Remarks{
			width: 90%;
			height: 50px;
			display: flex;
			justify-content: start;
			align-items: center;
			text{
				color:#888888;
				margin-right: 50rpx;
				font-size: 30rpx;
			}
		}
	}
	.ActualPayment{
		width: 100%;
		height: 55px;
		position: fixed;
		bottom: 5rpx;
		left: 0rpx;
		background-color: #FFFFFF;
		box-shadow: 0 -1px 5px rgb(0 0 0 / 20%);
		display: flex;
		justify-content: start;
		align-items: center;
		p{
			margin:0rpx 30rpx;
			color: #606266;
			font-size: 30rpx;
		}
		text{
			color:#fa436a;
			font-size: 30rpx;
		}
		.Submit{
			width: 270rpx;
			height: 100%;
			position: absolute;
			right: 0rpx;
			top:0rpx;
			color: #FFFFFF;
			font-size: 40rpx;
			background-color: #999999;
		}
	}
}
</style>
